<template>
  <div class="info">
    <v-data-table
      :headers="headers"
      :items="desserts"
      :search="search"
      sort-by="viewCount"
      update: sort-desc
      class="elevation-1"
    >
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title><span style="color: #fff; text-shadow: 0 0 5px #000"><b>DANH SÁCH THỐNG KÊ</b></span></v-toolbar-title>
          <v-spacer></v-spacer>
          <div style="margin-right: 20px">
            <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Tìm kiếm"
              single-line
              hide-details
            ></v-text-field>
          </div>
          <!-- <v-dialog v-model="dialog" max-width="500px">
            <template v-slot:activator="{ on, attrs }">
              <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">
                Thêm kênh
              </v-btn>
            </template>
            <v-card>
              <v-card-title>
                <span class="headline">{{ formTitle }}</span>
              </v-card-title>

              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12">
                      <v-text-field
                        v-model="editedItem.link"
                        label="Link kênh"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close"> Hủy </v-btn>
                <v-btn color="blue darken-1" text @click="save"> Lưu </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog> -->
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="headline"
                >Are you sure you want to delete this item?</v-card-title
              >
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete"
                  >Cancel</v-btn
                >
                <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                  >OK</v-btn
                >
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon small class="mr-2" @click="editItem(item)"> mdi-pencil </v-icon>
        <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
      </template>
      <!-- <template v-slot:no-data>
      <v-btn color="primary" @click="initialize"> Reset </v-btn>
    </template> -->
    </v-data-table>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      search: "",
      dialog: false,
      dialogDelete: false,
      headers: [
        { text: "Tài khoản", value: "account", sortable: false },
        {
          text: "Tên kênh",
          align: "start",
          sortable: false,
          value: "name",
        },
        { text: "Đăng ký", value: "subscriberCount" },
        { text: "Lượt xem", value: "viewCount" },
        { text: "Số video", value: "videoCount" },
        // { text: "Actions", value: "actions", sortable: false },
      ],
      desserts: [],
      editedIndex: -1,
      editedItem: {
        link: "",
      },
      defaultItem: {
        link: "",
      },
      accountList: [],
      idList: [
        {
          account: "thuyanaws5",
          link: "https://www.youtube.com/channel/UCHY4r7pzC9zk3rXv07oiUBg",
        },
        {
          account: "thuyanaws4",
          link: "https://www.youtube.com/channel/UC5NDeVkGH8pqgO_aPZX4_ng",
        },
        {
          account: "thuyanaws3",
          link: "youtube.com/channel/UCAzWBtuzGRQgYUjeE3a8zHA",
        },
        {
          account: "thuyanaws2",
          link: "https://www.youtube.com/channel/UC_XzhJmA-384xc0HqtEkGJQ",
        },
        {
          account: "thuatthuat107",
          link: "https://www.youtube.com/channel/UCh7_ORD7zIRbsKGXONoaMKg",
        },
        {
          account: "thuatt684",
          link: "https://www.youtube.com/channel/UC0nxwoF0sBVhp_zlG6ldzHg",
        },
        {
          account: "tthuat37",
          link: "https://www.youtube.com/channel/UCCO_CeW_1E7gxm1K0_kY8_Q",
        },
        {
          account: "thuatthuat189",
          link: "https://www.youtube.com/channel/UCsmrUdgHwTikouTOYojCYrw",
        },
        {
          account: "thuatthuat083",
          link: "https://www.youtube.com/channel/UCjKtTOHo_ec_eQQYE3DnMAQ",
        },
        {
          account: "thuatthuat06",
          link: "https://www.youtube.com/channel/UCRRHyCjT7oQXhL7Y2avoIzw",
        },
        {
          account: "thuatthuat724 ",
          link: "https://www.youtube.com/channel/UCbIof3YU6_MFDH3uer3NqvQ",
        },
        {
          account: "thuatthuat66 ",
          link: "https://www.youtube.com/channel/UCcCDyCY2Xe683G6PDjjia9w",
        },
        {
          account: "thuatthuat10",
          link: "https://www.youtube.com/channel/UCsETLJiBlVmOyB8ZgUrrGsw",
        },
        {
          account: "thuatthuat15",
          link: "https://www.youtube.com/channel/UC4kusjwpe0U8UcNCTMG2MCg",
        },
        {
          account: "tthuat37",
          link: "https://www.youtube.com/channel/UCCO_CeW_1E7gxm1K0_kY8_Q",
        },
        {
          account: "thuatthuat18",
          link: "UC3HpUpKBNdOvzceWqdKR7-A",
        },
        {
          account: "thuatthuat81",
          link: "UCJb-7jiY_ymN2la11VrQoQg",
        },
        {
          account: "thuatthuat85",
          link: "UCprEGYRuBh4ul2VOrf_4ciw",
        },
        {
          account: "tthuat71",
          link: "https://www.youtube.com/channel/UCR2zsAxj1XBtfCbZoxGMSZA",
        },
        {
          account: "thuatt717",
          link: "UCuRWGehqt1MZU1W0ftKfsmA",
        },
        {
          account: "nikolebillups8776",
          link: "https://studio.youtube.com/channel/UClRFqDWnfAQRIc__AvDTgYA",
        },
        {
          account: "brennanichole7731",
          link: "https://studio.youtube.com/channel/UCvUXuKS5ofVpEOBNrnFqwKg",
        },
        {
          account: "thuyanaws9",
          link: "https://www.youtube.com/channel/UCHSa5Myjrqv5Uy0g6-7VkeA",
        },
        {
          account: "thuyanaws8",
          link: "youtube.com/channel/UCM2znmdS29QSSFJgcpyWIMQ",
        },
        {
          account: "thuyanaws6",
          link: "youtube.com/channel/UCPeoUvS5TRmUsbxhjOFzfeQ",
        },
      ],
    };
  },
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Thêm kênh" : "Edit Item";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  created() {
    this.initialize();
  },

  methods: {
    initialize() {
      this.desserts = this.accountList;
    },

    editItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.desserts.splice(this.editedIndex, 1);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      if (this.editedIndex > -1) {
        Object.assign(this.desserts[this.editedIndex], this.editedItem);
      } else {
        this.desserts.push(this.editedItem);
      }
      this.close();
    },

    formatNumber(value) {
      return String(value)
        .toString()
        .replace(/\B(?=(\d{3})+(?!\d))/g, ".");
    },
  },
  mounted() {
    for (let [index, item] of this.idList.entries()) {
      axios
        .get("https://www.googleapis.com/youtube/v3/channels", {
          params: {
            part: "statistics, snippet",
            key: "AIzaSyAyNYkNtBMdxgl6M5GIzP9s1LUVJjp2oKk",
            id: item.link.slice(item.link.length - 24, item.link.length),
          },
        })
        .then((response) => {
          const profile = response.data.items[0];
          this.accountList.push({
            account: item.account,
            name: profile.snippet.title,
            subscriberCount: this.formatNumber(
              profile.statistics.subscriberCount
            ),
            videoCount: this.formatNumber(profile.statistics.videoCount),
            viewCount: profile.statistics.viewCount,
          });
        });
    }
  },
};
</script>
<style>
.info {
  background: linear-gradient(to bottom right, #003366 0%, #ff0066 100%);
  height: 100vh;
  overflow: hidden;
}
.v-data-table {
  background: rgba(255, 255, 255, 0.4) !important;
  margin: 0 auto;
  height: 100vh;
}
.v-sheet {
  background: none !important;
}
.v-data-footer {
  background: none !important;
}
.theme--light.v-text-field:not(.v-input--has-state):hover
  > .v-input__control
  > .v-input__slot:before {
  display: none !important;
}
.theme--light.v-text-field > .v-input__control > .v-input__slot:before {
  display: none !important;
}
tbody tr:hover {
  background: linear-gradient(
    to bottom right,
    #7182a3 0%,
    #e86ba4 100%
  ) !important;
}
</style>




